import { generateRandomArray } from '../../../../../../utils';
const SkSocialAccounts = () => {
    return <div className="">
        {generateRandomArray(2).map((number, index) => (<div key={index} className="d-flex align-items-center pt-2 pb-2">
            <div className="d-flex align-items-center me-2 flex-grow-1 overflow-hidden">
                <div className="sk_img symbol symbol-38 rounded-circle flex-shrink-0 me-2 overflow-hidden"></div>
                <div className="overflow-hidden w-100" style={{ maxWidth: 130 }}>
                    <p className="sk_line sk_line_100 sk_line_height_10 mb-2"></p>
                    <span className="sk_line sk_line_50 sk_line_height_8 mb-0 d-block"></span>
                </div>
            </div>
            <span className="d-flex align-items-end flex-column flex-shrink-0" style={{ width: 100 }}>
                <p className="sk_line sk_line_80 sk_line_height_10 mb-2"></p>
                <span className="sk_line sk_line_100 sk_line_height_8 mb-0 d-block"></span>
            </span>
        </div>
        ))}
    </div>

}

export default SkSocialAccounts;
