import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Link } from "react-router-dom";
import { calculatePercentage, kFormatter, generateRandomArray } from "../../../../../utils";
import { OverlayTrigger, Tooltip, Row, Col, Card, Badge } from "react-bootstrap";
import { baseUrl, FontAwesomeIcon } from "../../../../../Assets/AssetsHelper"


class Overview extends Component {
    render() {
        const { overview, loader } = this.props
        const color = ['blue', 'orange', 'yellow', 'green']
        return <div className="businessOverview_section">
            <h4 className="h3">Business Overview</h4>
            <Row>
                {overview && !loader ? overview.map((Item, Index) => {
                    const total = Item.total ? parseInt(Item.total) : 0
                    const total_last_week = Item.total_last_week ? parseInt(Item.total_last_week) : 0
                    const status = total > total_last_week ? true : false

                    return <Col lg={3} sm={6} key={Index}>
                        <Link to={`/analytics`}>
                            <Card className={`gutter-b card-shadow ${color[Index]}`}>
                                <Card.Body>
                                    <div className="f-between">
                                        <p className="fs-8 fw-medium text-muted mb-2">
                                            {Item.label}
                                        </p>
                                        <OverlayTrigger placement='top' overlay={<Tooltip>{Item.tool_tip}</Tooltip>} >
                                            <span><FontAwesomeIcon type={'solid'} icon={'circle-info'} iconClass={'text-muted'}/></span>
                                        </OverlayTrigger>
                                    </div>
                                    <div className="d-flex align-items-center">
                                        <p className="h1 mb-0">{kFormatter(total)}</p>
                                        {total && total_last_week ? <Badge bg='' className={`badge-light-${status ? `success` : 'danger'} ms-1 py-1`}>
                                            <FontAwesomeIcon type={'solid'} icon={status ? 'caret-up' : 'caret-down'} iconClass={'me-1'}/>
                                            {calculatePercentage(total, total_last_week).toFixed(0)}%
                                        </Badge> : null}
                                    </div>
                                </Card.Body>
                            </Card>
                        </Link>
                    </Col>
                })
                    :
                    generateRandomArray(4).map((count, Index) => (<Col lg={3} sm={6} key={Index}>
                        <Card className="gutter-b">
                            <Card.Body>
                                <p className="mb-6 sk_line sk_line_50 sk_line_height_10"></p>
                                <p className="fs-8 fw-medium text-muted mb-3 sk_line sk_line_30 sk_line_height_20"></p>
                            </Card.Body>
                        </Card>
                    </Col>))}
            </Row>
        </div>
    }

}

const mapStateToProps = state => {
    return {
        overview: state.dashboardData.overview,
        loader: state.dashboardData.overview_loader
    }
}

export default connect(mapStateToProps, null)(Overview)