import React, { Component } from 'react';
import ReactHtmlParser from 'react-html-parser';
import { connect } from 'react-redux';
import { Link } from "react-router-dom";
import { GET_NETWORK_ICON, GET_NETWORK_NAME, showAddAccounntAlert } from "../../../../../utils";
import { Col, Card, Button, Image } from "react-bootstrap";
import SkSocialAccounts from "./SkeletonComponents/SkSocialAccounts";
import { baseUrl, FontAwesomeIcon } from "../../../../../Assets/AssetsHelper";
import NoCardData from "../../CommonComponents/NotFound/NoCardData";
import AuthorIcon from "../../CommonComponents/Author/AuthorIcon";
import withRouter from '../../../../routes/WithRouter';
import { checkPermission } from '../../../../../actions/collaboratorActions';

const AccountList = ({feed, item, index, reconnectAccount}) => {
    const isConnected = !feed ? item.ConnectedAccounts.api == 2 ? true : false : false
    return  <div className="d-flex align-items-center pt-2 pb-2" key={index}>
        <div className="d-flex align-items-center flex-grow-1">
            {feed ? 
            <span className="h-38px w-38px rounded-circle d-flex align-items-center flex-shrink-0 me-1">
                <Image src={baseUrl(`media/icons/social-icons-square/${GET_NETWORK_ICON(item.Network.id)}.svg`)} height={43} width={43} alt={GET_NETWORK_NAME(item.Network.id)} fluid/>
            </span>
            :
            <AuthorIcon imageSize={38} 
            symbolClass={'symbol-label fw-bolder text-uppercase fs-5'} 
            className={'symbol symbol-38 rounded-circle flex-shrink-0 me-2 overflow-hidden'} 
            name={item.ConnectedAccounts.accountName} profileUrl={item.ConnectedAccounts.profilePicture} networkId={item.Network.id} />
            }
            <div className="d-flex text-left flex-column conn_user_info w-75">
                <h5 className="w-75 text-nowrap overflow-hidden d-block text-ellipsis mb-0">{feed ? ReactHtmlParser(item.Feed.feedName): ReactHtmlParser(item.ConnectedAccounts.accountName)}</h5>
                {!feed ? <small className="text-nowrap overflow-hidden d-block text-ellipsis">@{item.ConnectedAccounts.accountName}</small> : null}
            </div>
        </div>
        <div className="d-flex flex-column align-items-end">
            {isConnected ? <div className="text-success d-flex fs-8">
                <span className="me-1">
                    <FontAwesomeIcon type={'solid'} icon={'check'}/>
                </span>
                <span className="fw-bold">Connected</span>
            </div>
            : <Button variant="link" onClick={() => reconnectAccount({networkId: item.Network.id, networkName: item.Network.name})}>Reconnect</Button>}
            <span className='text-gray-600 fs-9'>{item.Network.name} Profile</span>
        </div>
    </div>
}
class SocialAccount extends Component{
    
    reconnectAccount = (obj) => {
        const item = { id: obj.networkId, name: (obj.networkName).toLowerCase() }
        showAddAccounntAlert(item, true, false)
    }
    
    connectAcc = () => {
        if(checkPermission('networkManager', 'manageAccount')) this.props.navigate("/social-account")
    }

    render(){
        const { loader, accounts } = this.props;
        const feeds = accounts?.feeds
        const allAccounts = accounts?.connectedAccounts
        return <Col sm={6}>
            <Card className="card-stretch gutter-b">
                <Card.Body>
                    <div className="f-between mb-3">
                        <div className="h4">Social Accounts</div>
                        {allAccounts && allAccounts.length > 5 ? <Link to={`/social-account`} className="btn btn-link">View all</Link> : null}
                    </div>
                        {loader ? 
                        <div className="pe-2" style={{height:160, overflowX:'auto'}}>
                            <SkSocialAccounts /> 
                        </div>
                        :
                        allAccounts && allAccounts.length > 0 ?
                        <div className="pe-2" style={{height:160, overflowX:'auto'}}> 
                            {feeds && feeds.map((item, index) => {
                                    return <AccountList key={index} feed={true} item={item} index={index} reconnectAccount={this.reconnectAccount}/>
                                })}
                                
                            {allAccounts.map((item, index) => {
                                return <AccountList key={index} feed={false} item={item} index={index} reconnectAccount={this.reconnectAccount}/>
                            })}
                        </div> :
                        <div style={{height:184}}>
                            <NoCardData title="Add your Social Accounts" desc="Connecting your social media platform is the first step." methodTitle="Connect" method={this.connectAcc} />
                        </div>
                        }
                </Card.Body>
            </Card>
        </Col>
    }
    
}

const mapStateToProps = state => {
    return {
        accounts: state.dashboardData.accounts,
        loader: state.dashboardData.accounts_loader
    }
}

export default connect(mapStateToProps,null)(withRouter(SocialAccount))