import React, { Component, Suspense } from 'react';
import {ProgressBar, Button, Col, Card, Row, OverlayTrigger, Tooltip} from 'react-bootstrap'; 
import { FontAwesomeIcon } from '../../../../../Assets/AssetsHelper';
import { kFormatter, OPEN_INTERCOM } from '../../../../../utils';

const Feature = (props) => {
    const {title, tooltip, total, using} = props
    let progress = parseInt((using / (total ? total : 100)) * 100);
    const getColor = e => { 
        if (e >= 70) {
            return 'danger';
        } else if (e >= 50) {
            return 'warning';
        } else if (e >= 0) {
            return 'success';
        } else {
            return 'unknown';
        }
    }
    return <Card className="card-stretch gutter-b shadow-none border">
        <Card.Body>
            <div className="f-between mb-1">
                <div className="fs-7 fw-medium">{title}</div>
                <OverlayTrigger placement='top' overlay={<Tooltip>{tooltip}</Tooltip>} >
                    <span><FontAwesomeIcon type={'solid'} icon={'circle-info'} iconClass={'text-muted'}/></span>
                </OverlayTrigger>
            </div>
            <div className='f-between w-100 mb-3'>
                <div className="display-3 fw-medium text-dark">{kFormatter(using > 0 ? using : 0)}</div>
                {progress >= 50 ? <Button variant='outline-primary' size={'sm'} className='ms-3' onClick={OPEN_INTERCOM}>
                    Talk to Sales
                </Button>
                :null}
            </div>
            <div className="d-flex flex-column">
                <ProgressBar variant={getColor(progress)} className="h-10px mb-1" now={progress?progress:0} />
                <span className="text-gray-600 fs-9 fw-medium"><span className='text-dark'>{using?using:0}</span> used out of {total?total:0}</span>
            </div>
        </Card.Body>
    </Card>
}

class FeatureUsage extends Component{
    render(){
        const { productData } = this.props
        return <Card className='gutter-b'>
                <Card.Body>
                    <div className="h4 mb-4">Feature Usage</div>
                    <Row className="gx-4">
                        <Col md={4}>
                            <Suspense fallback={null}>
                                <Feature title={'Gallery Views'} tooltip={'Total number of views received on your shoppable gallery.'} gallery={true} total={productData?.featuresLimit[2].total} using={productData?.featuresLimit[2].using} />
                            </Suspense>
                        </Col>
                        <Col md={4}>
                            <Suspense fallback={null}>
                                <Feature title={'Retained Posts'} tooltip={'Total number of posts collected out of the available limit of posts.'} gallery={true} total={productData?.featuresLimit[5].total} using={productData?.featuresLimit[5].using} />
                            </Suspense>
                        </Col>
                        <Col md={4}>
                            <Suspense fallback={null}>
                                <Feature title={'Tag Products'} tooltip={'Total number of products tagged in your shoppable gallery.'} gallery={true} total={productData?.featuresLimit[4].total} using={productData?.featuresLimit[4].using} />
                            </Suspense>
                        </Col>
                    </Row>
                </Card.Body>
            </Card>
        
    }
}

export default FeatureUsage