import React, { Component } from 'react';
import { connect } from 'react-redux';
import { OverlayTrigger, Tooltip, Col, Card, ProgressBar, Button } from "react-bootstrap";
import { FontAwesomeIcon } from "../../../../../Assets/AssetsHelper";
import { OPEN_INTERCOM } from '../../../../../utils';

class OverallCount extends Component {
    render(){
        const {title, tooltip, userRule, gallery, dashBoard} = this.props
        const total = gallery ? userRule?.walls : userRule?.feeds
        const count = gallery ? dashBoard?.numberOfActiveWalls : dashBoard?.allfeedsCount
        let progress = parseInt((count / (total ? total : 100)) * 100);
        const getColor = e => { 
            if (e >= 70) {
                return 'danger';
            } else if (e >= 50) {
                return 'warning';
            } else if (e >= 0) {
                return 'success';
            } else {
                return 'unknown';
            }
        }
        return <Card className="card-50 card-stretch gutter-b">
            <Card.Body>
                <div className="f-between mb-3">
                    <div className="h4">{title}</div>

                    {progress >= 50 ? <Button variant='outline-primary' size={'sm'} className='ms-3' onClick={OPEN_INTERCOM}>
                        <FontAwesomeIcon icon={'comment'} iconClass={'me-1'}/>
                        Talk to Sales
                    </Button>
                    :
                    <OverlayTrigger placement='top' overlay={<Tooltip>{tooltip}</Tooltip>} >
                        <span><FontAwesomeIcon type={'solid'} icon={'circle-info'} iconClass={'text-muted'}/></span>
                    </OverlayTrigger>
                    }
                </div>
                <div className="d-flex flex-column">
                    <div className="h4">{count?count:0}/{total?total:0}</div>
                    <ProgressBar variant={getColor(progress)} className="h-10px" now={progress?progress:0} />
                </div>
            </Card.Body>
        </Card>
}}

const mapStateToProps = state => {
    return {
        userRule: state.authorized?.userData?.user_data?.UserRule,
        dashBoard:state.dashboardData?.dashboarddata
    }
}   

export default connect(mapStateToProps)(OverallCount)