import React, { Component, Suspense } from 'react';
import { connect } from 'react-redux';
import moment from 'moment';
import withRouter from '../../../../routes/WithRouter';
import { Container, Button, Row, Col } from 'react-bootstrap';
import { GET_WEBSITE_LIST } from '../../../../../actions/websiteAction';
import { FontAwesomeIcon } from '../../../../../Assets/AssetsHelper';
import { GET_ALL_SHOPON_LIST } from '../../../../../actions/shoponAction';
import { GET_DASHBOARD_DATA, TAGGSHOP_PRODUCT_SUBSCRIPTION_DATA } from '../../../../../actions/sceneActions';
import DateRangePicker from 'react-bootstrap-daterangepicker';
import ErrorFound from '../../../../ErrorFound';
import "../home.scss"
import { GET_IFRAM } from '../../../../../utils';
import { DASHBOARD_LOAD } from '../../../../../reducer/keys';


import CaseStudy from './CaseStudy';
import Overview from './Overview';
import SocialAccount from './SocialAccount';
import OverallCount from './OverallCount';
import PublishAssets from './PublishAssets';
import ChannelPerfomance from './ChannelPerfomance';
import ContentChart from './ContentChart';
import ActiveUsers from './ActiveUsers';
import TopPosts from './TopPosts';
import TopProducts from './TopProducts';
import FeatureUsage from './FeatureUsage';
import Swal from 'sweetalert2';
import { isMobile } from 'react-device-detect';


// const CaseStudy = React.lazy(() => import("./CaseStudy"));
// const Overview = React.lazy(() => import("./Overview"));
// const SocialAccount = React.lazy(() => import("./SocialAccount"));
// const OverallCount = React.lazy(() => import("./OverallCount"));
// const PublishAssets = React.lazy(() => import("./PublishAssets"));
// const ChannelPerfomance = React.lazy(() => import("./ChannelPerfomance"));
// const ContentChart = React.lazy(() => import("./ContentChart"));
// const ActiveUsers = React.lazy(() => import("./ActiveUsers"));
// const TopPosts = React.lazy(() => import("./TopPosts"));
// const TopProducts = React.lazy(() => import("./TopProducts"));
// const FeatureUsage = React.lazy(() => import("./FeatureUsage"));


const dateRanges = {
    "Today": [moment().format("DD-MM-YYYY"), moment().format("DD-MM-YYYY")],
    "Yesterday": [moment().subtract(1, 'days').format("DD-MM-YYYY"), moment().subtract(1, 'days').format("DD-MM-YYYY"),],
    'Last 7 Days': [moment().subtract(6, 'days').format("DD-MM-YYYY"), moment().format("DD-MM-YYYY"),],
    'Last 30 Days': [moment().subtract(30, 'days').format("DD-MM-YYYY"), moment().format("DD-MM-YYYY"),],
    'Last Month': [moment().subtract(1, 'month').startOf('month').format("DD-MM-YYYY"), moment().subtract(1, 'month').endOf('month').format("DD-MM-YYYY"),],
}

class Dashboard extends Component {
    state = { loadScroll: GET_IFRAM() ? true : true, startDate: moment().subtract(30, 'days').format("DD-MM-YYYY"), endDate: moment().format("DD-MM-YYYY"), maxDate: moment().endOf('month').format("DD-MM-YYYY") }
    componentDidMount() {
        const { getDashboard, websiteList, getWebistes, shopOnList, getShopOn, dashboardLoad, productData } = this.props
        const { startDate, endDate } = this.state
        if (!(productData && Object.keys(productData).length > 0)) this.props.TAGGSHOP_PRODUCT_SUBSCRIPTION_DATA();
        getDashboard(startDate, endDate);
        if (!(websiteList && websiteList.length)) getWebistes()
        if (!(shopOnList && shopOnList.length)) getShopOn()

        // if (GET_IFRAM()) {
        //     let timeout = setTimeout(() => {
        //         dashboardLoad();
        //         clearTimeout(timeout);
        //     }, 1500);
        //     window.addEventListener('scroll', this.handleScroll);
        // }

       // this.checkTour()

    }

    // componentWillUnmount() {
    //     if (GET_IFRAM()) window.removeEventListener('scroll', this.handleScroll);
    // }

    checkTour = () => {
        const { login_number, navigate } = this.props
        if (login_number == 1 && !GET_IFRAM() && !localStorage.getItem('isTour') && !isMobile) {
            Swal.fire({
                title: `Welcome!`,
                text: `Welcome to Tagshop! Lets get started and create your first shoppable gallery. Easily manage and showcase your top social content and UGC.`,
                showCancelButton: true,
                cancelButtonText: "Skip Tour",
                allowOutsideClick: false,
                confirmButtonText: "Create Gallery",
                customClass: {
                    actions: 'swal2-actions-reverse',
                }

            }).then((result) => {
                if (result.value) {
                    localStorage.setItem('isTour', true)
                    localStorage.setItem('tourStep', 1)
                    navigate('/content/moderation')
                }
                else if (result.dismiss === "cancel") localStorage.setItem('isTour', false)
            });
        }
    }

    // componentWillUnmount() {
    //     if (GET_IFRAM()) window.removeEventListener('scroll', this.handleScroll);
    // }
      

    handleScroll = () => this.setState({ loadScroll: true })

    handleOnApply = (event, picker) => this.setState({ startDate: picker.startDate.format("DD-MM-YYYY"), endDate: picker.endDate.format("DD-MM-YYYY") })

    handleCallback = (start, end) => {
        this.props.getDashboard(start, end)
        this.setState({ startDate: start.format("DD-MM-YYYY"), endDate: end.format("DD-MM-YYYY") })
    }

    render() {
        const { productData } = this.props
        const { startDate, endDate, maxDate, loadScroll } = this.state
        const { loadDelay } = this.props

        return <Container>
            <div className="text-end w-100">
                <ErrorFound>
                    <DateRangePicker onApply={this.handleOnApply} startDate={startDate} endDate={endDate}
                        initialSettings={{
                            locale: { format: 'DD-MM-YYYY', separator: '-' },
                            maxDate: maxDate,
                            showDropdowns: true,
                            startDate: startDate, endDate: endDate,
                            ranges: dateRanges,
                        }} onCallback={this.handleCallback}>
                        <Button variant='secondary' className='mb-5 f-center flex-grow-1' style={{ minWidth: 190 }}>
                            <FontAwesomeIcon icon={'calendar-range'} iconClass={'me-1'} />
                            {moment(startDate, "DD-MM-YYYY").format("ll")}
                            -
                            {moment(endDate, "DD-MM-YYYY").format("ll")}
                        </Button>
                    </DateRangePicker>
                </ErrorFound>
            </div>
        <CaseStudy />
              {/* { loadDelay || !GET_IFRAM()  ? <> */}
            <Overview />
                <Row>
                    <ErrorFound>
                        <SocialAccount />
                    </ErrorFound>
                    <Col sm={6}>
                        <OverallCount title={'Gallery Count'} tooltip={'No. of galleries created vs the total no. of galleries available in your plan.'} gallery={true} />
                        <OverallCount title={'Feed count'} tooltip={'No. of feeds created vs the total no. of feeds available in your plan.'} />
                    </Col>
                </Row>

                <FeatureUsage productData={productData} />

                {/* </> : null} */}

               {/* { loadScroll ? <> */}
            <PublishAssets />
            <ChannelPerfomance startDate={startDate} endDate={endDate} />
                <Row>
                    <Col lg={6}>
                        <ErrorFound><ContentChart /></ErrorFound>
                    </Col>
                    <Col lg={6}>
                        <ErrorFound><ActiveUsers /></ErrorFound>
                    </Col>
                    <Col lg={6}>
                        <ErrorFound><TopPosts /></ErrorFound>
                    </Col>
                    <Col lg={6}>
                        <ErrorFound><TopProducts /></ErrorFound>
                    </Col>
                </Row> 
                {/* </> : null } */}
        </Container>
    }
}

const mapStateToProps = state => {
    return {
        productData: state.userAccountData?.productSubscriptions,
        websiteList: state.websiteList?.websiteList,
        shopOnList: state.shopOnList?.shopOnList,
        loadDelay: state.dashboardData?.loadDelay,

    }
}

const mapDispatchToProps = dispatch => {
    return {
        getDashboard: (start, end) => dispatch(GET_DASHBOARD_DATA(start, end)),
        getWebistes: () => dispatch(GET_WEBSITE_LIST()),
        getShopOn: () => dispatch(GET_ALL_SHOPON_LIST()),
        dashboardLoad: () => dispatch({ type: DASHBOARD_LOAD }),
        TAGGSHOP_PRODUCT_SUBSCRIPTION_DATA: () => dispatch(TAGGSHOP_PRODUCT_SUBSCRIPTION_DATA()),
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(withRouter(React.memo(Dashboard)))